

const page = () => {
  const Photos = [
    { id: 1, image: "/Vehivle/bus.jpeg" },
    { id: 2, image: "/Vehivle/EV-van.png" },
    { id: 3, image: "/Vehivle/haice.png" },
    { id: 4, image: "/Vehivle/haice.png" },
  ];
  return (
    <div className="my-20  p-20 flex justify-between flex-row mx-auto">
      <div>
        <div>
          <Image width={500} height={500} src="/Vehivle/bus.jpeg" alt=".." />
        </div>
        <Swiper
          modules={[Navigation, Pagination, Scrollbar, A11y]}
          // spaceBetween={20}
          // slidesPerView={3}
          navigation
          pagination={{ clickable: true }}
        >
          <SwiperSlide>
            <div
              className="grid grid-cols-2
               gap-2
                sm:grid-cols-3
             "
            >
              {Photos.map((photo) => (
                <div
                  key={photo.id}
                  className="relative h-24 w-full bg-gray-200 rounded-md overflow-hidden"
                >
                  <Image
                    src={photo.image}
                    alt={`Photo ${photo.id}`}
                    fill
                    className="object-cover"
                  />
                </div>
              ))}
            </div>
          </SwiperSlide>
        </Swiper>
      </div>
      <div className="w-3xl">
        Information part Lorem ipsum dolor sit amet consectetur adipisicing
        elit. Dolorum error ea sunt ex ratione unde corrupti eveniet nostrum
        labore debitis.
      </div>
    </div>
  );
};

export default page;
